#filter {
  padding: 20px;
  background-color: #688718; }

.filter-btn {
  position: relative;
  text-align: center;
  font-weight: bold;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #222222;
  cursor: pointer; }
  .filter-btn::after {
    display: block;
    content: "";
    clear: both; }
  .filter-btn .filter-arrow {
    position: absolute;
    top: 3px;
    right: 20px;
    float: right;
    font-size: 1.5em; }
    .filter-btn .filter-arrow.rotateEffect {
      transition: all 1s cubic-bezier(0.19, 1, 0.22, 1);
      -webkit-transform: rotate(180deg);
              transform: rotate(180deg);
      top: 13px; }

.filter-checkbox {
  display: none;
  padding: 20px;
  background-color: #ffffff; }
  .filter-checkbox .form-check {
    margin: 10px 0; }
  .filter-checkbox .form-check-input {
    top: -2px;
    cursor: pointer; }
  .filter-checkbox .form-check-label {
    font-weight: bold;
    text-transform: uppercase;
    cursor: pointer; }

#htmlSpecialsCharacters {
  padding: 20px 0;
  background-color: #f5f5f5; }
  #htmlSpecialsCharacters pre {
    min-height: 44px;
    padding: 10px;
    text-align: center;
    background-color: #f5f5f5;
    border-radius: 3px; }

.character {
  padding: 10px; }
  .character[attr-type="null"] {
    display: none; }
  .character.displayCheckedInput {
    display: block; }
  .character.hideCheckedInput {
    display: none; }

.character-box {
  padding: 10px;
  text-align: center;
  background-color: #ffffff;
  border-radius: 3px; }
  .character-box hr {
    border-color: #f5f5f5;
    border-width: 3px; }
  .character-box span {
    display: block;
    padding: 10px;
    color: #688718;
    border-radius: 3px;
    cursor: pointer; }

.character-view {
  min-height: 120px;
  margin-bottom: 10px;
  padding: 10px;
  font-size: 2.5em;
  line-height: 2.5em;
  font-weight: bold;
  text-align: center; }

.character-html-encode {
  padding: 10px;
  font-size: 0.95em;
  text-align: center; }

.popover {
  font-weight: bold;
  text-align: center;
  background-color: #b0bf84;
  border-width: 0; }
  .popover.bs-popover-top .arrow::before,
  .popover.bs-popover-auto[x-placement="top"] .arrow::before {
    border-top-color: #ffffff; }
  .popover.bs-popover-top .arrow::after,
  .popover.bs-popover-auto[x-placement="top"] .arrow::after {
    border-top-color: #b0bf84; }
  .popover.bs-popover-left .arrow::before,
  .popover.bs-popover-auto[x-placement="left"] .arrow::before {
    border-left-color: #ffffff; }
  .popover.bs-popover-left .arrow::after,
  .popover.bs-popover-auto[x-placement="left"] .arrow::after {
    border-left-color: #b0bf84; }
  .popover.bs-popover-right .arrow::before,
  .popover.bs-popover-auto[x-placement="right"] .arrow::before {
    border-right-color: #ffffff; }
  .popover.bs-popover-right .arrow::after,
  .popover.bs-popover-auto[x-placement="right"] .arrow::after {
    border-right-color: #b0bf84; }
  .popover.bs-popover-bottom .arrow::before,
  .popover.bs-popover-auto[x-placement="bottom"] .arrow::before {
    border-bottom-color: #ffffff; }
  .popover.bs-popover-bottom .arrow::after,
  .popover.bs-popover-auto[x-placement="bottom"] .arrow::after {
    border-bottom-color: #b0bf84; }
  .popover .popover-body {
    color: #ffffff; }

.no-flexbox .character {
  display: block;
  float: left; }

.no-flexbox .clearFixForFlex::after {
  display: block;
  content: "";
  clear: both; }

footer {
  padding: 30px;
  background-color: #4b4f3c;
  color: #ffffff; }
  @media screen and (max-width: 575px) {
    footer {
      padding: 30px 10px; } }
  footer .row:first-child {
    padding-bottom: 30px; }
  footer .technoLogo {
    display: none; }

.copyright {
  text-align: center; }

.conception-tools div {
  padding: 20px;
  text-align: center;
  background-color: #222222;
  border-radius: 3px; }

.conception-tools svg,
.conception-tools i,
.conception-tools img {
  margin: 10px 10px 0 10px; }

.conception-tools .bootstrap {
  width: 30px;
  height: 30px;
  border-width: 1px;
  border-color: #ffffff;
  border-style: solid;
  border-radius: 3px;
  vertical-align: -.125em; }

.conception-tools .jquery {
  width: 30px;
  height: 30px;
  vertical-align: -.125em; }

.conception-tools .handlebars {
  width: 30px;
  height: auto;
  vertical-align: top;
  margin-top: 22px; }

.conception-tools .json {
  width: 50px;
  height: auto;
  vertical-align: top;
  margin-top: 18px; }

.conception-tools .browsersync {
  width: auto;
  height: 30px;
  vertical-align: -.125em; }

.no-flexbox footer .fab {
  display: none !important; }

.no-flexbox footer .technoLogo {
  display: inline-block;
  color: #ffffff; }

.no-flexbox footer img {
  margin: 0 10px; }
  .no-flexbox footer img.handlebars {
    margin-top: 12px; }
  .no-flexbox footer img.json {
    margin-top: 2px;
    width: 32px;
    height: 32px; }
  .no-flexbox footer img.browsersync {
    width: 32px;
    height: 32px; }
